"use client";

import Image from "next/image";
import { useState, useEffect, useCallback } from "react";
import { slides } from "../data/slides";

export default function HeroSlider() {
  const [current, setCurrent] = useState(0);
  const [fadeTitle, setFadeTitle] = useState(true);
  const [fadeDesc, setFadeDesc] = useState(true);
  const [fadeButtons, setFadeButtons] = useState(true);

  const length = slides.length;

  // Functions
  const staggerFadeIn = useCallback(() => {
    setTimeout(() => setFadeTitle(true), 50);
    setTimeout(() => setFadeDesc(true), 200);
    setTimeout(() => setFadeButtons(true), 350);
  }, []);

  const fadeOutElements = useCallback((callback: () => void) => {
    setFadeTitle(false);
    setFadeDesc(false);
    setFadeButtons(false);

    setTimeout(() => {
      callback();
      staggerFadeIn();
    }, 300);
  }, [staggerFadeIn]);

  const handleNextSlide = useCallback(() => {
    fadeOutElements(() => setCurrent((prev) => (prev + 1) % length));
  }, [fadeOutElements, length]);

  const handlePrevSlide = useCallback(() => {
    fadeOutElements(() => setCurrent((prev) => (prev - 1 + length) % length));
  }, [fadeOutElements, length]);

  // Auto-rotation
  useEffect(() => {
    const timer = setInterval(handleNextSlide, 5000);
    return () => clearInterval(timer);
  }, [handleNextSlide]);

  return (
    <div className="relative w-full h-[90vh] overflow-hidden">
      {slides.map((slide, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-1000 ease-in-out ${
            index === current ? "opacity-100 z-10" : "opacity-0 z-0"
          }`}
        >
          <Image
            src={slide.src}
            alt={slide.title}
            fill
            className="object-cover pointer-events-none"
            priority={index === 0}
          />

          {/* Overlay */}
          <div className="absolute inset-0 bg-black/40 flex flex-col justify-center items-center text-center px-6 pointer-events-auto">
            {/* Title */}
            <h2
              className={`text-5xl md:text-6xl font-bold text-white drop-shadow-lg mb-4 transform transition-all duration-700 ${
                fadeTitle && index === current ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
              }`}
            >
              {slide.title}
            </h2>

            {/* Description */}
            <p
              className={`max-w-2xl text-lg text-gray-200 drop-shadow-md mb-6 transform transition-all duration-700 ${
                fadeDesc && index === current ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
              }`}
            >
              {slide.description}
            </p>

            {/* Buttons */}
            <div
              className={`flex flex-col sm:flex-row gap-4 transform transition-all duration-700 ${
                fadeButtons && index === current ? "opacity-100 translate-y-0" : "opacity-0 translate-y-6"
              }`}
            >
              <a
                href={slide.blogLink}
                target="_blank"
                rel="noopener noreferrer"
                className="px-6 py-3 bg-white text-black font-semibold rounded-lg shadow hover:bg-gray-100 transition transform hover:-translate-y-1 hover:scale-105 duration-300"
              >
                Read About This Destination
              </a>
              <a
                href={slide.bookingLink}
                target="_blank"
                rel="noopener noreferrer"
                className="px-6 py-3 bg-sky-600 text-white font-semibold rounded-lg shadow hover:bg-sky-700 transition transform hover:-translate-y-1 hover:scale-105 duration-300"
              >
                Book a Tour
              </a>
            </div>
          </div>
        </div>
      ))}

      {/* Navigation buttons */}
      <button
        onClick={handlePrevSlide}
        className="absolute left-6 top-1/2 -translate-y-1/2 bg-black/50 hover:bg-black/70 text-white p-4 rounded-full z-10 cursor-pointer"
      >
        ❮
      </button>
      <button
        onClick={handleNextSlide}
        className="absolute right-6 top-1/2 -translate-y-1/2 bg-black/50 hover:bg-black/70 text-white p-4 rounded-full z-10 cursor-pointer"
      >
        ❯
      </button>

      {/* Dot Indicators */}
      <div className="absolute bottom-6 left-1/2 -translate-x-1/2 flex space-x-2 z-10">
        {slides.map((_, index) => (
          <button
            key={index}
            onClick={() => fadeOutElements(() => setCurrent(index))}
            className={`w-3 h-3 rounded-full ${
              index === current ? "bg-white" : "bg-gray-400"
            }`}
          />
        ))}
      </div>
    </div>
  );
}
